import { Table, Tooltip } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom';
import {
    RollbackOutlined
} from "@ant-design/icons";

const Quote = () => {

    const [quote, setQuote] = useState([]);
    const [name, setName] = useState([]);

    let { Symbol } = useParams();

    console.log(Symbol)


    const fetchQuote = async () => {
        const res = await axios.get(`https://prototype.sbulltech.com/api/v2/quotes/${Symbol}`).catch((err) => {
            console.log("err", err)
        });

        var filterData = res.data.payload
        var keys = Object.keys(filterData);
        var values = keys.map(function (key) {
            return filterData[key];
        });
        setQuote(values[0])
        setName(keys[0])
    }
    console.log(quote)

    useEffect(() => {
        fetchQuote()
    }, [])

    const columns = [
        {
            title: "Price",
            dataIndex: "price",
            render: (text, item) => {
                const data = item.price.toFixed(3)
                return (
                    data
                )
            }
        },
        {
            title: "Time",
            dataIndex: "time",
            defaultSortOrder: "descend",
            sorter: (a, b) => {
                var nameA = a.time.toLowerCase(); // ignore upper and lowercase
                var nameB = b.time.toLowerCase(); // ignore upper and lowercase
                if (nameA < nameB) {
                    return -1;
                }
                if (nameA > nameB) {
                    return 1;
                }

                return 0;
            },
        },
        {
            title: "Valid Till",
            dataIndex: "valid_till",

        },
    ]
    return (
        <>
            <div
                style={{
                    display: "flex",
                    justifyContent: "center",
                    fontSize: "2em",
                    marginTop: "10px",
                    fontWeight: "600",
                    color: "rgb(43 205 63)",
                    textShadow: "0px 0px 2px #619121"

                    // backgroundColor: "rgb(76 185 89)"
                }}
            >
                <div>Quote Page</div>
            </div>
            <div
                style={{
                    display: "flex",
                    justifyContent: "start",
                    margin: "35px 0 0",
                    fontSize: "2em",
                    fontWeight: "600",
                    padding: "7px 2%",
                    color: "white",
                    backgroundColor: "rgb(82 106 161)"
                }}
            >
                <div>
                    <Tooltip title="Back to Home">
                        <Link to={`/`}>
                            <RollbackOutlined style={{ color: "white" }} />
                        </Link>
                    </Tooltip>
                </div>
                &nbsp;&nbsp;
                <div>{name} Data</div>

            </div>
            <Table
                columns={columns}
                pagination={false}
                dataSource={quote}
            />
        </>
    )
}

export default Quote
