import { Button, Input, Space, Table, Tooltip } from 'antd';
import axios from 'axios';
import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
import Papa from 'papaparse';
import {
    FilterFilled,
    SearchOutlined,
    StockOutlined,
} from "@ant-design/icons";
import Highlighter from 'react-highlight-words';

const InstrumentTable = () => {

    const [tableData, setTableData] = useState([])
    const [searchText, setSearchText] = useState("");
    const [searchedColumn, setSearchedColumn] = useState("");

    //fetch table data
    const fetchProduct = async () => {
        const res = await axios.get("https://prototype.sbulltech.com/api/v2/instruments").catch((err) => {
            console.log("err", err)
        });
        Papa.parse(res.data, {
            header: true,
            complete: results => {
                setTableData(results.data)
            },
        });
    }

    console.log(tableData)

    // search for symbol and name

    function getColumnSearchProps(dataIndex) {
        return {
            filterDropdown: ({
                setSelectedKeys,
                selectedKeys,
                confirm,
                clearFilters,
            }) => (
                <div style={{ padding: 8 }}>
                    <Input
                        // ref={node => {
                        //   this.searchInput = node;
                        // }}
                        placeholder={`Search ${dataIndex}`}
                        value={selectedKeys[0]}
                        onChange={(e) =>
                            setSelectedKeys(e.target.value ? [e.target.value] : [])
                        }
                        onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
                        style={{ width: 240, marginBottom: 8, display: "block" }}
                    />
                    <Space>
                        <Button
                            type="primary"
                            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
                            icon={<SearchOutlined />}
                            size="small"
                            style={{ width: 90 }}
                        >
                            Search
                        </Button>
                        <Button
                            onClick={() => handleReset(clearFilters)}
                            size="small"
                            style={{ width: 90 }}
                        >
                            Reset
                        </Button>
                        <Button
                            type="link"
                            size="small"
                            onClick={() => {
                                confirm({ closeDropdown: false });
                                setSearchText(selectedKeys[0]);
                                setSearchedColumn(dataIndex);
                            }}
                        >
                            Filter
                        </Button>
                    </Space>
                </div>
            ),
            filterIcon: (filtered) => (
                <Tooltip title="Search Name"><SearchOutlined style={{ color: filtered ? "#1890ff" : "Red", width: "20%" }} /></Tooltip>
            ),
            onFilter: (value, record) =>
                record[dataIndex]
                    .toString()
                    .toLowerCase()
                    .includes(value.toLowerCase()),
            onFilterDropdownVisibleChange: (visible) => {
                if (visible) {
                }
            },
            render: (text) =>
                searchedColumn === dataIndex ? (
                    <Highlighter
                        highlightStyle={{ backgroundColor: "#ffc069", padding: 0 }}
                        searchWords={[searchText]}
                        autoEscape
                        textToHighlight={text.toString()}
                    />
                ) : (
                    text
                ),
        };
    }

    function handleSearch(selectedKeys, confirm, dataIndex) {
        confirm();
        setSearchText(selectedKeys[0]);
        setSearchedColumn(dataIndex);
    }

    function handleReset(clearFilters) {
        clearFilters();
        setSearchText("");
    }
    useEffect(() => {
        fetchProduct()
    }, [])




    const columns = [
        {
            title: 'Symbol',
            dataIndex: 'Symbol',
            ...getColumnSearchProps("Symbol"),
            render: (text, item) => {
                return (
                    <>
                        <Link to={`/product/${item.Symbol}`}>
                            {item.Symbol}
                        </Link>
                    </>
                )
            }
        },
        {
            title: 'Name',
            dataIndex: 'Name',
        },

        {
            title: 'Sector',
            dataIndex: 'Sector',
            filters: [
                { text: "Cement & Cement Products", value: "Cement & Cement Products" },
                { text: "Information Technology", value: "Information Technology" },
                { text: "Healthcare Services", value: "Healthcare Services" },
                { text: "Consumer Goods", value: "Consumer Goods" },
                { text: "Energy", value: "Energy" },
                { text: "Chemicals", value: "Chemicals" },
                { text: "Pharma", value: "Pharma" },
                { text: "Construction", value: "Construction" },
                { text: "Financial Services", value: "Financial Services" },
                { text: "Services", value: "Services" },
            ],
            filterIcon: filtered => <Tooltip title="Filter"><FilterFilled style={{ color: filtered ? "#1890ff" : "Red", width: "20%" }} /></Tooltip>,
            onFilter: (value, record) => {
                return record.Sector === value;
                // sorter: (a, b) => a.type > b.type,
            },
        },
        {
            title: 'Valid Till',
            dataIndex: 'Validtill',

        },
    ];
    return (
        <>

            <div
                style={{ maxHeight: "100vh" }}
            >
                <div
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        padding: "10px",
                        fontSize: "2em",
                        fontWeight: "600",
                        color: "white",
                        backgroundColor: "rgb(217 98 169)"
                    }}
                >
                    <div>Stock Page</div>
                    &nbsp;
                    <div><StockOutlined /></div>
                </div>
                <div style={{}}>
                    <Table
                        dataSource={tableData}
                        columns={columns}
                        pagination={false}
                        scroll={{ y: 500 }}
                    />;
                </div>

            </div>
        </>
    )
}

export default InstrumentTable
